.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}
.checkoutProductInfo{
    padding-left: 20px;

}
.checkoutProductInfo > button {
    background-color: #f0c14b;
    border: 1 px solid;
    margin-top: 10px;
    border-color: #a88743 #9c7e31 #846a29;
    color: #111;
}
.checkoutProductImage{
    object-fit: contain;
    width: 100px;
    height: 180px;
}
.checkoutProductRating{
    display: flex;
}
.checkoutProductTitle{
    font-size: 17px;
    font-weight: 800;
}